import cn from "classnames";
import { useEffect, useRef, useState } from "react";
import { TablesPaginationInfo } from "@wptb/utils/react";
import Modal from "@wptb/ui/components/modal";
import { ACTIONS, applyTableAction } from "./http-actions";
import { EllipsisLoadingIcon } from "@wptb/ui/icons/loaders";
import notify from "@/public/notify";
import { CopyIcon } from "@wptb/ui/icons";
import { copyToClipboard } from "@wptb/utils/ui";

interface NormalListProps {
  apiData: TablesPaginationInfo;
  setPage: (page: string, params: any) => void;
  isTrash?: boolean;
  setTables: (tables: any, sort?: boolean) => void;
  sortBy?: string;
  sortOrder?: string;
  setSort: (by: string) => void;
}

const NormalList = ({
  apiData,
  setPage,
  isTrash,
  setTables,
  sortBy,
  sortOrder,
  setSort,
}: NormalListProps) => {
  const selectionMap = useRef<Record<number, boolean>>({});
  const loadingMap = useRef<Record<number, boolean>>({});
  const [bulkAction, setBulkAction] = useState("");
  const [checkCount, setCheckCount] = useState(0);
  const [modal, setModal] = useState<{
    action: string;
    targetId?: number;
    targetTitle?: string;
  } | null>(null);

  useEffect(() => {
    selectionMap.current = {};
    setCheckCount(0);
  }, [apiData]);

  const startEditing = (table: number) => {
    setPage("builder", {
      table,
    });
  };

  const checkAll = () => {
    if (checkCount < apiData.posts.length) {
      apiData.posts.forEach((post) => {
        selectionMap.current[post.id] = true;
      });
      setCheckCount(apiData.posts.length);
    } else {
      selectionMap.current = {};
      setCheckCount(0);
    }
  };

  const checkOne = (table: number) => {
    if (selectionMap.current[table]) {
      delete selectionMap.current[table];
      setCheckCount(checkCount - 1);
    } else {
      selectionMap.current[table] = true;
      setCheckCount(checkCount + 1);
    }
  };

  const applyAction = async () => {
    if (!modal) {
      return;
    }
    if (modal.targetId) {
      if (loadingMap.current[modal.targetId]) {
        return;
      }
      loadingMap.current[modal.targetId] = true;
    } else
      for (const i in selectionMap.current) {
        if (loadingMap.current[i]) {
          return;
        }
        loadingMap.current[i] = selectionMap.current[i];
      }
    const idsx = Object.keys(selectionMap.current).map(Number);
    selectionMap.current = {};
    setCheckCount(0);
    setModal(null);

    const { action, id, ids, res } = await applyTableAction({
      action: modal.action,
      id: modal.targetId,
      ids: idsx,
    });

    if (ids?.length) {
      ids.forEach((id) => {
        delete loadingMap.current[id];
      });
    } else {
      delete loadingMap.current[id!];
    }

    if (res.error) {
      notify(res.error, "error", 0);
      setTables([...apiData.posts]);
      return;
    }

    if (res.message) {
      notify(res.message, "success");
    }

    if (action === "trash" || action === "restore" || action === "delete") {
      setTables([...apiData.posts.filter((post) => post.id !== id)]);
    } else if (action === "trash_bulk" || action === "restore_bulk") {
      setTables([...apiData.posts.filter((post) => !ids?.includes(post.id))]);
    } else {
      const newPosts = [...apiData.posts];
      res.posts?.forEach((post: any) => {
        newPosts.push(post);
      });
      setTables(newPosts, true);
    }
  };

  const modalInfo = modal && ACTIONS[modal.action];
  sortBy = sortBy || "id";

  return (
    <>
      <div className="wptb-v2-bulk-actions">
        <select
          value={bulkAction}
          onChange={(e) => setBulkAction(e.target.value)}
        >
          <option value="">Bulk Actions</option>
          {isTrash ? (
            <option value="restore_bulk">Restore</option>
          ) : (
            <option value="trash_bulk">Delete</option>
          )}
        </select>
        <button
          type="button"
          onClick={() =>
            checkCount > 0 &&
            setModal({
              action: bulkAction,
            })
          }
        >
          Apply
        </button>
        <p>
          <b>{checkCount}</b> items selected
        </p>
      </div>

      <table
        className={cn({
          "wptb-v2-table-list": !apiData.loading,
          "wptb-v2-table-loading": apiData.loading,
        })}
      >
        <colgroup>
          <col style={{ width: "30px" }} />
          <col />
          <col />
          <col />
          <col />
        </colgroup>
        <thead>
          <tr>
            <th>
              <input
                type="checkbox"
                checked={checkCount === apiData.posts.length}
                onChange={checkAll}
              />
            </th>
            <th>
              Title
              <button
                type="button"
                onClick={() => setSort("title")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc":
                    sortBy === "title" && sortOrder === "asc",
                  "wptb-sort-btn-desc":
                    sortBy === "title" && sortOrder !== "asc",
                })}
              />
            </th>
            <th>
              Shortcode
              <button
                type="button"
                onClick={() => setSort("id")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc": sortBy === "id" && sortOrder === "asc",
                  "wptb-sort-btn-desc": sortBy === "id" && sortOrder !== "asc",
                })}
              />
            </th>
            <th>
              Created
              <button
                type="button"
                onClick={() => setSort("date")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc": sortBy === "date" && sortOrder === "asc",
                  "wptb-sort-btn-desc":
                    sortBy === "date" && sortOrder !== "asc",
                })}
              />
            </th>
            <th>
              Last Modified
              <button
                type="button"
                onClick={() => setSort("modified")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc":
                    sortBy === "modified" && sortOrder === "asc",
                  "wptb-sort-btn-desc":
                    sortBy === "modified" && sortOrder !== "asc",
                })}
              />
            </th>
          </tr>
        </thead>
        <tbody>
          {apiData.loading ? (
            Array(5)
              .fill(0)
              .map((_, i) => (
                <tr key={i}>
                  <td>
                    <div />
                  </td>
                  <td>
                    <div />
                  </td>
                  <td>
                    <div />
                  </td>
                  <td>
                    <div />
                  </td>
                  <td>
                    <div />
                  </td>
                </tr>
              ))
          ) : !apiData.posts.length ? (
            <tr>
              <td colSpan={5}>
                <p>No results found</p>
              </td>
            </tr>
          ) : (
            apiData.posts.map((post) => (
              <tr key={post.id}>
                <td>
                  <input
                    type="checkbox"
                    checked={selectionMap.current[post.id]}
                    onChange={() => checkOne(post.id)}
                  />
                  {loadingMap.current[post.id] && (
                    <div className="wptb-v2-table-pending">
                      {EllipsisLoadingIcon}
                    </div>
                  )}
                </td>
                <td>
                  <p>{post.title || "[Untitled]"}</p>
                  {!isTrash ? (
                    <div className="wptb-v2-table-actions">
                      <button
                        className="wptb-table-action-edit"
                        type="button"
                        onClick={() => startEditing(post.id)}
                      >
                        Edit
                      </button>
                      <button
                        className="wptb-table-action-duplicate"
                        type="button"
                        onClick={() =>
                          setModal({
                            action: "duplicate",
                            targetId: post.id,
                            targetTitle: post.title,
                          })
                        }
                      >
                        Duplicate
                      </button>
                      <button
                        type="button"
                        className="wptb-table-action-preview"
                      >
                        Preview
                      </button>
                      <button
                        className="wptb-table-action-trash"
                        type="button"
                        onClick={() =>
                          setModal({
                            action: "trash",
                            targetId: post.id,
                            targetTitle: post.title,
                          })
                        }
                      >
                        Trash
                      </button>
                    </div>
                  ) : (
                    <div className="wptb-v2-table-actions">
                      <button
                        className="wptb-table-action-restore"
                        type="button"
                        onClick={() =>
                          setModal({
                            action: "restore",
                            targetId: post.id,
                            targetTitle: post.title,
                          })
                        }
                      >
                        Restore
                      </button>
                      <button
                        className="wptb-table-action-trash"
                        type="button"
                        onClick={() =>
                          setModal({
                            action: "delete",
                            targetId: post.id,
                            targetTitle: post.title,
                          })
                        }
                      >
                        Delete
                      </button>
                    </div>
                  )}
                </td>
                <td>
                  <div className="wptb-v2-shortcode-col">
                    [wptb id={post.id}]
                    <button
                      type="button"
                      className="wptb-v2-copy-btn"
                      onClick={() => {
                        copyToClipboard(`[wptb id=${post.id}]`);
                        notify("Copied to clipboard!", "success");
                      }}
                    >
                      {CopyIcon}
                    </button>
                  </div>
                </td>
                <td>{post.date}</td>
                <td>{post.modified}</td>
              </tr>
            ))
          )}
        </tbody>
        <tfoot>
          <tr>
            <th>
              <input
                type="checkbox"
                checked={checkCount === apiData.posts.length}
                onChange={checkAll}
              />
            </th>
            <th>
              Title
              <button
                type="button"
                onClick={() => setSort("title")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc":
                    sortBy === "title" && sortOrder === "asc",
                  "wptb-sort-btn-desc":
                    sortBy === "title" && sortOrder !== "asc",
                })}
              />
            </th>
            <th>
              Shortcode
              <button
                type="button"
                onClick={() => setSort("id")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc": sortBy === "id" && sortOrder === "asc",
                  "wptb-sort-btn-desc": sortBy === "id" && sortOrder !== "asc",
                })}
              />
            </th>
            <th>
              Created
              <button
                type="button"
                onClick={() => setSort("date")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc": sortBy === "date" && sortOrder === "asc",
                  "wptb-sort-btn-desc":
                    sortBy === "date" && sortOrder !== "asc",
                })}
              />
            </th>

            <th>
              Last Modified
              <button
                type="button"
                onClick={() => setSort("modified")}
                className={cn("wptb-v2-table-sort-btn", {
                  "wptb-sort-btn-asc":
                    sortBy === "modified" && sortOrder === "asc",
                  "wptb-sort-btn-desc":
                    sortBy === "modified" && sortOrder !== "asc",
                })}
              />
            </th>
          </tr>
        </tfoot>
      </table>

      <div className="wptb-v2-bulk-actions">
        <select
          value={bulkAction}
          onChange={(e) => setBulkAction(e.target.value)}
        >
          <option value="">Bulk Actions</option>
          {isTrash ? (
            <option value="restore_bulk">Restore</option>
          ) : (
            <option value="trash_bulk">Delete</option>
          )}
        </select>
        <button
          type="button"
          onClick={() =>
            checkCount > 0 &&
            setModal({
              action: bulkAction,
            })
          }
        >
          Apply
        </button>
        <p>
          <b>{checkCount}</b> items selected
        </p>
      </div>
      {modalInfo && (
        <Modal
          onClose={() => setModal(null)}
          title={modalInfo.title}
          icon={modalInfo.icon}
          className={modalInfo.className}
        >
          <p
            dangerouslySetInnerHTML={{
              __html: modalInfo.text(modal, checkCount),
            }}
          />
          <div className="wptb-v2-modal-actions">
            <button type="button" onClick={applyAction}>
              Yes
            </button>
            <button type="button" onClick={() => setModal(null)}>
              No
            </button>
          </div>
        </Modal>
      )}
    </>
  );
};

export default NormalList;
